/* General section styling */
.section {
    padding: 4rem 2rem;
    background-color: var(--color-bg2);
}

/* Skill header */
.skill-header {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 2rem;
}

/* Mac container styling */
.maccontainer {
    position: relative;
    width: 60%;
    max-width: 1200px;
    margin: 0 auto;
    overflow: hidden;
}

/* Image wrapper styling */
.image-wrapper {
    position: relative;
    width: 100%;
    max-width: 100%;
    height: auto;
}

.image-wrapper img {
    width: 100%;
    height: auto;
    display: block;
}

