.icon-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    gap: 1rem;
    justify-content: center;
}

.icon-item {
    text-align: center;
}

.icon {
    width: 50px;
    height: 50px;
    margin-bottom: 0.5rem;
}

.icon-title {
    font-size: 0.9rem;
    color: #333;
}